@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

html{
  scroll-behavior: smooth;
}

.container-fluid{
  max-width: 95%;
  @media screen and (max-width: 1020px) {
    max-width: 75%;
  }
  @media screen and (max-width: 768px) {
    max-width: 95%;
  }
}

:root{
  --white: white;
  --amar: #414345;
  --navactive: #130f40;
  --navbar: #414345;
}

[data-theme="light"] {
  --white: #2c3e50;
  --amar: #dfe6e9;
  --navbar: #dfe6e9;
}

.app-container{
  background: var(--amar);
  transition: .5s;
}


// Navbar...................................................................................


.navbar{
  background: var(--navbar);
  // background: linear-gradient(to right, #414345, #232526);
  height: 9vh;
  align-items: center;
  .logo{
    z-index: 1000;
    a{
      text-decoration: none;
      color: var(--white);
      font-size: 24px;
      letter-spacing: 1px;
      font-weight: bold;
    }
  }
  .wraper-sun{
    display: none;
    @media screen and (max-width: 1000px) { 
    display: flex;
    gap: 1rem;
    align-items: baseline;
    .fa-sun{
      color: yellow;
      font-size: 1.2rem;
    }
    }
  }
  .nav-menu{
      display: flex;
      list-style-type: none;
      margin: 0;
      @media screen and (max-width: 1000px) {
        flex-direction: column;
        position: absolute;
        background-color: #2C5364;
        top: 10px;
        left: 0;
        visibility: hidden;
        width: 100%;
        text-align: center;
        padding: 20px 0 !important;
        padding: 0;
      }
      .nav-items{
        a{
          text-decoration: none;
          color: var(--white);
          margin-right: 2rem;
          font-size: 19px;
          letter-spacing: 1.5px;
          transition: .5s ease-in-out;
          &:hover{
            color: #000;
          }
          @media screen and (max-width: 1000px) {
            margin-right: 0;
          }
        }
        @media screen and (max-width: 1000px) {
          padding: 15px 0;
        }
      }
      .sun-moon{
        padding-left: 1rem;
        padding-top: .2rem;  
        cursor: pointer; 
        @media screen and (max-width: 1000px) {
          display: none;
        }
        .fa-sun{
          color: yellow;
          font-size: 1.2rem;
        }
        .fa-moon{
          color: #232526;
          font-size: 1.2rem;
        }
      }
      .nav-items-btn{
        a{
          text-decoration: none;
          color: #fff;
          background-color: orange;
          padding: 10px 20px;
          border-radius: 20px;
          font-size: 19px;
          letter-spacing: 1.5px;
        }
        @media screen and (max-width: 1000px) {
          padding-top: 20px;
          padding-bottom: 20px;
        }
      }
  }
  .nav-menu.active{
    visibility: visible;
    transition: .5s ease-in-out;
  }
  .icons{
    display: none;
    @media screen and (max-width: 1000px) {
      display: block;
      color: var(--white);
      font-size: 20px;
      z-index: 10;
    }
  }
}
.navbar-active{
  height: 9vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  box-shadow: -1px 11px 24px -11px rgba(0,0,0,0.75);
  transition: .5s;
}


// BEAUTY.........................................................................


.beauty{
  height: auto;
  .beauty-content{
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 2rem;
    justify-items: center;
    @media screen and (max-width: 1020px) {
      grid-template-columns: 1fr;
      text-align: center;
    }
    .left{
      height: 90vh;
      @media screen and (max-width: 1020px) {
        height: auto;
      }
      .left-content{
        padding: 9rem 0;
        @media screen and (max-width: 1020px) {
          padding-bottom: 5rem;
        }
        h1{
          color: var(--white);
          font-size: 52px;
          font-weight: 600;
          span{
            color: orange;
          }
          @media screen and (max-width: 520px) {
            font-size: 40px;
          }
          @media screen and (max-width: 384px) {
            font-size: 30px;
          }
        }
        p{
          color: var(--white);
          font-size: 24px;
          padding: 2rem 0;
          font-weight: 300;
          @media screen and (max-width: 520px) {
            font-size: 20px;
          }
          @media screen and (max-width: 384px) {
            font-size: 16px;
          }
        }
        .form{
          padding-bottom: 6rem;
          @media screen and (max-width: 384px) {
            display: flex;
            flex-direction: column;
          }
          input{
            border: none;
            padding: 18px 20px;
            width: 100%;
            border-radius: 30px;
          }
          button{
            border: none;
            color: var(--white);
            background-color: orange;
            padding: 10px 20px;
            border-radius: 20px;
            margin-left: -130px;
            @media screen and (max-width: 384px) {
              margin-left: 0;
              width: 50%;
              margin: auto;
              margin-top: 2rem;
            }
          }
        }
        .left-img{
          h5{
            font-size: 18px;
            color: var(--white);
            opacity: .5;
            font-weight: 600;
          }
          img{
            width: 75%;
          }
        }
      }
    }
    .right{
      margin: auto;
      .image{
        width: 100%;
        img{
          padding-top: 4rem;
          max-width: 100%;
          max-height: 50%;
          @media screen and (max-width: 1445px) {
            max-width: 90%;
            margin-left: 2rem;
          }
        }
      }
    }
  }
}



// FEATURE..........................................................



.feature{
  padding: 4rem 0;
  height: auto;
  .header{
    padding: 2rem 0;
    text-align: center;
    h5{
      color: orange;
      letter-spacing: 1px;
    }
    h1{
      color: var(--white);
      font-weight: bold;
      font-size: 50px;
      span{
        color: orange;
      }
      @media screen and (max-width: 450px) {
        font-size: 30px;
      }
    }
    p{
      opacity: .4;
      color: var(--white);
      font-size: 22px;
      width: 70%;
      margin: auto;
      @media screen and (max-width: 991px) {
        width: 90%;
      }
      @media screen and (max-width: 640px) {
        width: 100%;
        font-size: 18px;
      }
    }
  }
  .cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    @media screen and (max-width: 1020px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 768px) {
      grid-template-columns: 1fr;
    }
    .box{
      display: flex;
      padding: 4rem 0;
      .logo{
        img{
          width: 80px;
          padding: 23px 25px;
          border-radius: 50%;
          background-color: #232526;
        }
      }
      .box-content{
        padding: 0 20px;
        h2{
          color: var(--white);
        }
        p{
          color: var(--white);
          font-size: 18px;
          opacity: .8;
        }
      }
    }
  }
}



// WORK...............................................................



.work{
  height: auto;
  padding: 4rem 0;
  .work-content{
    display: grid;
    grid-template-columns: 3fr 3fr;
    gap: 3rem;
    @media screen and (max-width: 1020px) {
      grid-template-columns: 1fr;
      text-align: center;
    }
    .left{
      h5{
        color: orange;
        letter-spacing: 1px;
        @media screen and (max-width: 455px) {
          font-size: 15px;
        }
      }
      h1{
        color: var(--white);
        font-size: 64px;
        font-weight: bold;
        padding: 2rem 0;
        span{
          color: orange;
        }
        @media screen and (max-width: 710px) {
          font-size: 50px;
        }
        @media screen and (max-width: 560px) {
          font-size: 40px;
        }
        @media screen and (max-width: 455px) {
          font-size: 25px;
        }
      }
      p{
        color: var(--white);
        font-size: 24px;
        opacity: .7;
        padding-bottom: 2rem;
        @media screen and (max-width: 710px) {
          font-size: 20px;
        }
        @media screen and (max-width: 710px) {
          font-size: 17px;
        }
        @media screen and (max-width: 455px) {
          font-size: 15px;
        }
      }
      button{
        border: none;
        background-color: orange;
        color: #fff;
        padding: 10px 25px;
        border-radius: 20px;
        letter-spacing: 1px;
      }
    }
    .right{
      .image{
        max-width: 100%;
        img{
          width: 100%;
          box-shadow: 0 0 20px 0 rgba(0,0,0,0.75);
          height: 500px;
          border-radius: 10px;
        }
      }
    }
  }
}


// STEPS..........................................................................


.steps{
  height: auto;
  padding: 9rem 0;
  .steps-content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    @media screen and (max-width: 1020px) {
      grid-template-columns: 1fr;
    }
    .left{
      margin: auto;
      .image{
        max-width: 100%;
        img{
          width: 100%;
          padding-top: 2rem;
        }
      }
    }
    .right{
      .right-content{
        padding-top: 1rem;
        padding-left: 6rem;
        @media screen and (max-width: 1020px) {
          padding-left: 0;
        }
        .head{
          h5{
            color: orange;
            font-weight: 400;
            letter-spacing: 1px;
          }
          h1{
            color: var(--white);
            padding-bottom: 3rem;
            font-weight: bold;
            font-size: 56px;
            span{
              color: orange;
            }
            @media screen and (max-width: 670px) {
              font-size: 45px;
            }
          }
          @media screen and (max-width: 1020px) {
            text-align: center;
          }
        }
        .cards{
          .step{
            display: flex;
            @media screen and (max-width: 1020px) {
              flex-direction: column;
              text-align: center;
            }
            .num{
              h1{
                color: var(--white);
                font-size: 45px;
                opacity: .5;
              }
            }
            .text{
              padding-top: .5rem;
              padding-left: 2rem;
              // width: 52%;
              @media screen and (max-width: 1020px) {
                padding-left: 0;
              }
              h3{
                font-size: 22px;
                color: var(--white);
              }
              p{
                color: var(--white);
                opacity: .7;
              }
            }
          }
        }
      }
    }
  }
}


// VALUES...........................................................................



.values{
  height: auto;
  // padding: 12rem 0;
  .values-content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    @media screen and (max-width: 1020px) {
      display: flex;
      flex-direction: column-reverse;
      text-align: center;
    }
    .left{
      .left-content{
        h5{
          color: orange;
          letter-spacing: 1px;
        }
        h1{
          color: var(--white);
          font-size: 70px;
          font-weight: bold;
          padding: 1rem 0;
          span{
            color: orange;
          }
          @media screen and (max-width: 1020px) {
            font-size: 50px;
          }
          @media screen and (max-width: 487px) {
            font-size: 30px;
          }
        }
        p{
          font-size: 23px;
          color: var(--white);
          opacity: .7;
          @media screen and (max-width: 487px) {
            font-size: 18px;
          }
        }
        .divs{
          display: flex;
          @media screen and (max-width: 1020px) {
            flex-direction: column;
          }
          .fa-dollar-sign{
            background-color: green;
            padding: 10px 15px;
            border-radius: 20px;
            opacity: .5;  
          }
          .fa-briefcase{
            background-color: red;
            padding: 10px 15px;
            border-radius: 30px;
            opacity: .5;
          }
          h4{
            padding-top: 3rem;
            color: var(--white);
          }
        }
        button{
          border: none;
          margin-top: 1rem;
          background-color: orange;
          color: #fff;
          padding: 10px 25px;
          border-radius: 20px;
        }
      }
    }
    .right{
      margin: auto;
      .image{
        max-width: 600px;
        img{
          padding-top: 9rem;
          width: 100%;
          @media screen and (max-width: 1020px) {
            padding-top: 0;
          }
        }
      }
    }
  }
}



// PRICING.......................................................................


.pricing{
  height: auto;
  padding: 10rem 0;
  .pricing-content{
    .head{
      text-align: center;
      padding-bottom: 5rem;
      h5{
        color: orange;
      }
      h1{
        color: var(--white);
        font-size: 52px;
        font-weight: bold;
        span{
          color: orange;
        }
        @media screen and (max-width: 487px) {
          font-size: 45px;
        }
      }
      p{
        opacity: .7;
        color: var(--white);
        font-size: 22px;
        // margin: 0 26rem;
        @media screen and (max-width: 487px) {
          font-size: 18px;
        }
      }
    }
    .cards{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 3rem;
      text-align: center;
      @media screen and (max-width: 1020px) {
        grid-template-columns: 1fr;
      }
      .cardz{
        background-color: #232526;
        border-radius: 15px;
        width: 100%;
        margin: auto;
        color: #fff;
        &:nth-child(2){
          background-color: #5058C6;
        }
        &:nth-child(1){
          border-top: 5px solid #67B26F;
        }
        &:nth-child(3){
          border-top: 5px solid #1cefff;
        }
        .card-head{
          h2{
            padding-top: 2rem;
            padding-bottom: .5rem;
            font-size: 22px;
            font-weight: 600;
          }
          h1{
            padding-bottom: .7rem;
            font-size: 48px;
            font-weight: bold;
          }
          h6{
            padding-bottom: .5rem;
            font-weight: bold;
            font-size: 22px;
            opacity: .5;
          }
        }
        .card-body{
          padding-top: 2rem;
          line-height: 2rem;
          h5{
            font-size: 22px;
            font-weight: 600;
            padding-bottom: .7rem;
          }
          p{
            font-weight: 500;
            opacity: .8;
          }
        }
        .footer{
          padding-top: .5rem;
          padding-bottom: 1.4rem;
          button{
            border: none;
            background-color: #1cefff;
            padding: 10px 30px;
            border-radius: 20px;
            color: #fff;
            font-weight: bold;
            letter-spacing: 2px;
            &:hover{
              background-color: #09d4e2;
              transition: .4s;
            }
          }
        }
        @media screen and (max-width: 1020px) {
          max-width: 60%;
        }
        @media screen and (max-width: 650px) {
          max-width: 80%;
        }
        @media screen and (max-width: 487px) {
          max-width: 100%;
        }
      }
    }
  }
}



// TESTIMONIALS.....................................................................



.testi{
  background-color: #000;
  height: auto;
  padding: 10rem 0;
  .testi-content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    .left{
      background-color: #67B26F;
    }
    .right{
      background-color: #5058C6;
    }
  }
}




.right-slide{
  background-color: #1cefff;
  display: flex;
  width: 100%;
  overflow: hidden;
}






