// STARTED.....................................................

.started{
  height: auto;
  .started-content{
    background: #0F2027;
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);
    border-radius: 20px;
    padding: 5rem 0;
    max-width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    @media screen and (max-width: 1046px) {
      grid-template-columns: 1fr;
    }
    .text{
      padding-left: 3rem;
      @media screen and (max-width: 1046px) {
        padding-left: 0;
        text-align: center;
      }
      h2{
        color: #fff;
        @media screen and (max-width: 1020px) {
          font-size: 26px;
        }
      }
    }
    .butn{
      padding-top: .5rem;
      @media screen and (max-width: 1046px) {
        padding-top: 4rem;
      }
      @media screen and (max-width: 487px) {
        display: flex;
        flex-direction: column;
        gap: 1rem;
      }
      button{
        border: none;
        margin-left: 1rem;
        // gap: 1rem;
        background: #e96443;
        background: linear-gradient(to right, #904e95, #e96443); 
        padding: 20px 55px;
        font-size: 20px;
        border-radius: 30px;
        color: #fff;
        transition: .5s ease-in-out;
        @media screen and (max-width: 1046px) {
          padding: 18px 40px;
        }
        &:hover{
          background: none;
          border: 1px solid #e96443;
        }
        &:nth-child(2){
          background: none;
          border: 1px solid #e96443;
          &:hover{
            background: #e96443;
            background: linear-gradient(to right, #904e95, #e96443); 
            transition: .6s ease-in-out;
          }
        }
      }
    }
  }
}




// Footer....................................................................................



.footer{
  height: auto;
  padding: 7rem 0;
  .footer-content{
    .up{
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      gap: 2rem;
      text-align: center;
      @media screen and (max-width: 1020px) {
        grid-template-columns: repeat(4, 1fr);
      }
      @media screen and (max-width: 487px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media screen and (max-width: 390px) {
        grid-template-columns: repeat(2, 1fr);
      }
      .ups{
        h4{
          font-size: 24px;
          font-weight: bold;
          color: var(--white);
        }
        ul{
          list-style-type: none;
          line-height: 3rem;
          padding: 0;
          li{
            font-weight: 500;
            color: var(--white);
            opacity: .7;
          }
        }
      }
    }
    .down{
      display: flex;
      justify-content: space-between;
      padding-top: 5rem;
      @media screen and (max-width: 1020px) {
        flex-direction: column;
        text-align: center;
      }
      .logo{
        h2{
          color: var(--white);
          font-weight: 600;
        }
      }
      .text{
        p{
          color: var(--white);
          opacity: .4;
          font-size: 20px;
        }
      }
      .icons{
        i{
          margin-left: 1rem;
          background-color: #fff;
          padding: 10px;
          border-radius: 20px;
        }
      }
    }
  }
}

