.scrollTop{
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 100;
  bottom: 5%;
  right: 5%;
  height: 4rem;
  width: 4rem;
  border: 3px solid transparent;
  border-radius: 3rem;
  transition: .5s ease-in-out;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover{
      box-shadow: none;
  }
  a{
      .fa-chevron-up{
          font-size: 2rem;
          color: var(--white);
      }
  }
}

.view{
  visibility: visible;
  opacity: 1;
}













